body {
  background: #fffffff2;
  min-height: 100vh;
  margin: 0;
  padding: 2rem;
  font-family: Segoe UI, sans-serif;
}

.list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin: 40px 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.post-item {
  background: #ffffffd9;
  border: 1px solid #ccc;
  border-radius: 18px;
  flex-direction: column;
  align-items: center;
  width: 300px;
  min-height: 400px;
  padding: 18px;
  display: flex;
  box-shadow: 0 4px 24px #0000001f;
}

.post-item img, img {
  object-fit: cover;
  border-radius: 12px;
  width: 260px;
  height: 260px;
  margin-bottom: 16px;
}

.post-item h2 {
  color: #4e68da;
  text-align: center;
  margin: 0 0 10px;
  font-size: 1.3em;
  font-weight: 600;
}

.post-item p {
  color: #333;
  text-align: center;
  margin-bottom: 18px;
  font-size: 1em;
}

button, .post-item button {
  cursor: pointer;
  background: #ffffff26;
  border: none;
  border-radius: 20px;
  margin: 0 auto 8px;
  padding: 6px 16px;
  font-size: 1em;
  display: block;
  box-shadow: 0 8px 24px #0003;
}

.add-post {
  color: #fff;
  background: #646efff2;
  border: none;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 16px #0000002e;
}

.hero-backdrop, .edit-backdrop {
  z-index: 2;
  visibility: hidden;
  background-color: #00000067;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.modal, .edit-modal {
  z-index: 3;
  background: #fffffff2;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  width: 500px;
  min-height: 400px;
  padding: 32px 24px;
  display: flex;
  box-shadow: 0 8px 24px #0003;
}

.form, .edit-form {
  flex-direction: column;
  align-items: center;
  gap: 18px;
  width: 100%;
  display: flex;
}

.modal-list, .edit-list {
  flex-direction: column;
  gap: 14px;
  width: 100%;
  display: flex;
}

.modal-item, .edit-item {
  flex-direction: column;
  gap: 6px;
  display: flex;
}
